html {
  overflow-x: hidden;
  scrollbar-width: thin;
  scrollbar-color: #008aff #dddddd;
}

html::-webkit-scrollbar {
  width: 8px;
}

html::-webkit-scrollbar-track {
  background-color: #DDD;
}

html::-webkit-scrollbar-thumb {
  background-color: #008AFF;
}

@font-face {
  font-family: "reg";
  src: url("../fonts/frutiger-lt-arabic-55-roman.ttf");
}

@font-face {
  font-family: "bold";
  src: url("../fonts/frutiger-lt-arabic-65-bold.ttf");
}

/*
=====
LODEARS
=====
*/
.owl-carousel .owl-item img {
  width: auto;
}

/* ------ upper bar start ------ */
.upper-bar {
  background-color: #232f3e;
  width: 100%;
  height: 36px;
  font-family: reg;
  font-size: 13px;
  color: #8b8d92;
}

.upper-bar .zx-contain {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.upper-bar a {
  text-decoration: none;
  color: #8b8d92;
  transition: all .3s ease-in-out;
}

.upper-bar a:hover {
  color: #008bff;
}

.upper-bar .features {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 9px 0;
}

.upper-bar .features .feature {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 0 15px;
}

.upper-bar .features .feature i {
  padding: 0 0 0 9px;
}

.upper-bar .features .feature p {
  padding: 0;
  margin: 0;
}

/* ------ upper bar end ------ */
/* ------ navbar start  ------ */
.fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  background-color: #FFF;
  padding: 12px 0 0 0 !important;
}

.logo-shrink {
  width: 160px !important;
}

.zx-search-form-top {
  margin: 16px 0 0 0 !important;
}

.user-info-top {
  margin: 16px 0 0 0 !important;
}

.nav-list-top {
  margin: 16px 0 0 0 !important;
}

.nav-bar {
  padding: 18px 0 0 0;
  transition: all .5s ease-in-out;
  box-shadow: 0px 0px 8px 0px rgba(22, 22, 22, 0.1);
  background-color: #fff;
}

.nav-bar .zx-contain {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-bar .logo {
  width: 206px;
  height: auto;
  transition: all .5s ease-in-out;
}

.nav-bar .zx-search-form {
  width: 560px;
  position: relative;
}

.nav-bar .zx-search-form input {
  width: 100%;
  height: 46px;
  outline: none;
  background-color: whitesmoke;
  border: none;
  border-radius: 50px;
  padding: 0 30px;
  transition: all .5s ease-in-out;
}

.nav-bar .zx-search-form input::placeholder {
  font-family: reg;
  font-size: 14px;
  color: #bbbbbb;
}

.nav-bar .zx-search-form .zx-search-btn {
  width: 38px;
  height: 38px;
  position: absolute;
  top: 4px;
  left: 7px;
  font-size: 14px;
  color: #FFF;
  background-color: #008bff;
  border: none;
  border-radius: 50%;
  outline: none;
  transition: all .5s ease-in-out;
}

.nav-bar .zx-search-form .zx-search-btn i {
  transition: all .3s ease-in-out;
}

.nav-bar .zx-search-form .zx-search-btn:hover i {
  transform: rotateY(360deg);
}

.nav-bar .user-info {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: reg;
  font-size: 14px;
  position: relative;
}

.nav-bar .user-info a {
  text-decoration: none;
  color: #252933;
}

.nav-bar .user-info .account {
  padding-left: 18px;
  transition: all .5s ease-in-out;
  position: relative;
}

.nav-bar .user-info .account i {
  padding-left: 9px;
}

.nav-bar .user-info .account:hover {
  color: #008bff;
}

.nav-bar .user-info .account:hover + .log-in {
  opacity: 1;
  visibility: visible;
}

.nav-bar .user-info .log-in {
  width: 100px;
  height: 60px;
  position: absolute;
  top: 40px;
  right: -4px;
  background-color: #008bff;
  color: #FFF;
  border-radius: 5px;
  padding: 8px 12px;
  opacity: 0;
  visibility: hidden;
  transition: all .5s ease-in-out;
}

.nav-bar .user-info .log-in a {
  text-decoration: none;
  color: #FFF;
  display: block;
  font-family: reg;
}

.nav-bar .user-info .log-in:hover {
  opacity: 1;
  visibility: visible;
}

.nav-bar .user-info .fav {
  padding-left: 18px;
  transition: all .5s ease-in-out;
}

.nav-bar .user-info .fav i {
  padding-left: 9px;
}

.nav-bar .user-info .fav:hover {
  color: #008bff;
}

.nav-bar .user-info .zx-on-card {
  width: 49px;
  height: 49px;
  border-style: solid;
  border-width: 1px;
  border-color: #ececec;
  border-radius: 50%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all .5s ease-in-out;
}

.nav-bar .user-info .zx-on-card i {
  font-size: 16px;
}

.nav-bar .user-info .zx-on-card span {
  width: 15px;
  height: 15px;
  font-family: reg;
  font-size: 12px;
  line-height: 1.5;
  color: white;
  background-color: #008bff;
  border-radius: 50%;
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-bar .user-info .zx-on-card:hover {
  color: #008bff;
}

.nav-bar .nav-list {
  font-family: reg;
  font-size: 16px;
  border: 1px solid #f4f4f4;
  margin: 26px 0 0 0;
}

.nav-bar .nav-list ul {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: row-reverse;
  padding: 13px 0;
  margin: 0;
}

.nav-bar .nav-list ul a {
  text-decoration: none;
  color: #252933;
  transition: all .4s ease-in-out;
}

.nav-bar .nav-list ul li {
  padding: 0;
  margin: 0 0 0 24px;
  transition: all .4s ease-in-out;
}

.nav-bar .nav-list ul li:hover a {
  color: #008AFF;
}

/* ------ navbar end ------ */
/* ------ main slider start ------ */
.mr-top {
  margin: 190px 0 0 0 !important;
}

.zx-main-slider {
  margin: 20px 0 0 0;
  position: relative;
}

.zx-main-slider .owl-carousel .owl-dots {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
}

.zx-main-slider .owl-carousel .owl-nav button.owl-next,
.zx-main-slider .owl-carousel .owl-nav button.owl-prev,
.zx-main-slider .owl-carousel button.owl-dot {
  background: rgba(0, 0, 0, 0.502);
  width: 8px;
  height: 8px;
  border-radius: 50%;
  margin-left: 7px;
  outline: none;
}

.zx-main-slider button.owl-dot.active {
  background: #FFF;
  width: 8px;
  height: 8px;
}

/* ------ main slider end ------ */
/* ------ categories start ------ */
.categories {
  margin: 60px 0 0 0;
}

.categories a {
  text-decoration: none;
  font-family: reg;
  font-size: 17px;
  color: #252933;
}

.categories .zx-contain {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.categories .zx-contain .category {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.categories .zx-contain .category .img-container {
  border-radius: 50%;
  background-color: #008bff;
  width: 140px;
  height: 140px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 0 22px 0;
  transition: all .5s ease-in-out;
}

.categories .zx-contain .category .img-container img {
  transition: all .5s ease-in-out;
}

.categories .zx-contain .category .img-container:hover img {
  animation: transformY-animation .5s infinite alternate;
}

@keyframes transformY-animation {
  0% {
    transform: translateY(-2px);
  }
  100% {
    transform: translateY(2px);
  }
}

/* ------ categories end ------ */
/* ------ special offer start ------ */
.sp-offers a {
  text-decoration: none;
}

.sp-offers .zx-offers-header {
  width: 100%;
  height: 48px;
  padding: 0 28px;
  margin: 60px 0 0 0;
  background-color: #f7f7f7;
  border-right: 3px solid #008bff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all .5s ease-in-out;
}

.sp-offers .zx-offers-header h3 {
  font-family: bold;
  font-size: 18px;
  color: #252933;
  padding: 0;
  margin: 0;
}

.sp-offers .zx-offers-header a {
  font-family: reg;
  font-size: 14px;
  color: #a8afb1;
  padding: 0;
  margin: 0;
}

.sp-offers .zx-offers-header a:hover {
  color: #008bff;
}

.sp-offers .zx-offers-header:hover {
  border-right: 6px solid #008bff;
  padding: 0 25px;
}

.sp-offers .zx-card {
  width: 208px;
  height: auto;
  margin: 22px 0 0 0;
}

.sp-offers .zx-card a {
  text-decoration: none;
}

.sp-offers .zx-card .img-container {
  position: relative;
  overflow: hidden;
  width: 208px;
  height: 208px;
  border-style: solid;
  border-width: 1px;
  border-color: #f2f2f2;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all .5s ease-in-out;
}

.sp-offers .zx-card .img-container img {
  transition: all .5s ease-in-out;
}

.sp-offers .zx-card .img-container .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  opacity: 0;
  transition: all .5s ease-in-out;
}

.sp-offers .zx-card .img-container .overlay .offer {
  width: 60px;
  height: 30px;
  position: absolute;
  left: -61px;
  top: 12px;
  background-color: #008bff;
  color: #FFF;
  font-family: reg;
  font-size: 12px;
  padding: 6px 0 0 0;
  border: 1px solid #FFF;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all .5s ease-in-out;
}

.sp-offers .zx-card .zx-card-info {
  width: 208px;
  height: 122px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.sp-offers .zx-card .zx-card-info h4 {
  font-family: reg;
  font-size: 15px;
  color: #252933;
  line-height: 1.333;
  text-align: right;
  padding: 0 6px;
  margin: 30px 0 16px 0;
  -moz-transform: matrix(1, 0, 0, 1.00113, 0, 0);
  -webkit-transform: matrix(1, 0, 0, 1.00113, 0, 0);
  -ms-transform: matrix(1, 0, 0, 1.00113, 0, 0);
}

.sp-offers .zx-card .zx-card-info .new-price, .sp-offers .zx-card .zx-card-info .old-price {
  display: block;
  padding: 0 6px;
  font-family: reg;
  font-size: 16px;
  line-height: 1.2;
}

.sp-offers .zx-card .zx-card-info .new-price {
  color: #008bff;
}

.sp-offers .zx-card .zx-card-info .old-price {
  text-decoration: line-through;
  color: #989797;
}

.sp-offers .zx-card:hover .img-container {
  opacity: .6;
}

.sp-offers .zx-card:hover .img-container img {
  transform: scale(1.06);
}

.sp-offers .zx-card:hover .img-container .overlay {
  opacity: 0;
}

.sp-offers .zx-card:hover .img-container .overlay .offer {
  left: -1px;
}

/* ------ special offer end ------ */
/* ------ panners start ------ */
.panners {
  margin: 60px 0 0 0;
}

.panners .zx-contain {
  display: flex;
  justify-content: center;
  align-items: center;
}

.panners .zx-contain .panner {
  overflow: hidden;
  transition: all .5s ease-in-out;
}

.panners .zx-contain .panner img {
  transition: all .5s ease-in-out;
}

.panners .zx-contain .panner:hover img {
  transform: scale(1.02);
}

.panners .zx-contain .panner:first-child {
  margin: 0 0 0 16px;
}

/* ------ panners end ------ */
/* ------ sponsor start ------ */
.sponsor a {
  text-decoration: none;
}

.sponsor .zx-offers-header {
  width: 100%;
  height: 48px;
  padding: 0 28px;
  margin: 60px 0 0 0;
  background-color: #f7f7f7;
  border-right: 3px solid #008bff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all .5s ease-in-out;
}

.sponsor .zx-offers-header h3 {
  font-family: bold;
  font-size: 18px;
  color: #252933;
  padding: 0;
  margin: 0;
}

.sponsor .zx-offers-header a {
  font-family: reg;
  font-size: 14px;
  color: #a8afb1;
  padding: 0;
  margin: 0;
}

.sponsor .zx-offers-header:hover {
  border-right: 6px solid #008bff;
  padding: 0 25px;
}

.sponsor .sponsor-logos {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0 0 0;
}

.sponsor .sponsor-logos .sponsor-logo {
  width: 156px;
  height: 156px;
  border-style: solid;
  border-width: 1px;
  border-color: #f2f2f2;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all .5s ease-in-out;
}

.sponsor .sponsor-logos .sponsor-logo:hover {
  opacity: .6;
}

/* ------ sponsor end ------ */
/* ------ footer start ------ */
.footer {
  margin: 60px 0 0 0;
}

.footer a {
  text-decoration: none;
}

.footer .subscribe-bar {
  width: 100%;
  height: 97px;
  background-color: #008bff;
}

.footer .subscribe-bar .zx-contain {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 31px 0;
}

.footer .subscribe-bar .zx-contain h3 {
  padding: 0;
  margin: 1px 22px 0 37px;
  font-family: bold;
  font-size: 16px;
  line-height: 1.438;
  color: white;
}

.footer .subscribe-bar .zx-contain .e-mail {
  width: 609px;
  height: 45px;
  position: relative;
  transition: all .5s ease-in-out;
}

.footer .subscribe-bar .zx-contain .e-mail input {
  width: 100%;
  height: 100%;
  background-color: white;
  border: none;
  border-radius: 50px;
  padding: 0 33px 0 26px;
  outline: none;
}

.footer .subscribe-bar .zx-contain .e-mail input::placeholder {
  font-family: reg;
  font-size: 14px;
  color: #8c8b8b;
  line-height: 1.643;
}

.footer .subscribe-bar .zx-contain .e-mail button {
  background: transparent;
  border: none;
}

.footer .subscribe-bar .zx-contain .e-mail i {
  font-size: 16px;
  color: #008bff;
  width: 15px;
  height: 16px;
  position: absolute;
  left: 28px;
  top: 50%;
  transform: translateY(-50%);
  transition: all .3s ease-in-out;
  cursor: pointer;
}

.footer .subscribe-bar .zx-contain .e-mail:hover i {
  animation: translateX-animation .5s infinite alternate;
}

.footer .contact-us {
  background-color: #252933;
}

.footer .contact-us .zx-contain {
  display: flex;
  margin: 60px 0 60px 0;
}

.footer .contact-us .zx-contain .contact-info h3 {
  font-family: bold;
  font-size: 18px;
  color: white;
  line-height: 1.444;
  margin: 0 0 36px 0;
  cursor: default;
}

.footer .contact-us .zx-contain .contact-info ul {
  padding: 0;
  margin: 0;
}

.footer .contact-us .zx-contain .contact-info ul li {
  font-family: reg;
  font-size: 15px;
  color: white;
  line-height: 1.444;
  margin: 0 0 15px 0;
  transition: all .5s ease-in-out;
}

.footer .contact-us .zx-contain .contact-info ul li:hover {
  color: #21aac5;
}

.footer .contact-us .zx-contain .contact-info ul li i {
  width: 12px;
  font-size: 14px;
  color: #008bff;
  margin: 0 0 0 12px;
}

.footer .contact-us .zx-contain .contact-info .icons {
  margin: 30px 0 0 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.footer .contact-us .zx-contain .contact-info .icons a {
  text-decoration: none;
  color: #FFF;
}

.footer .contact-us .zx-contain .contact-info .icons .icon-shape {
  border-radius: 50%;
  background-color: #363a43;
  width: 36px;
  height: 36px;
  margin: 0 0 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all .5s ease-in-out;
}

.footer .contact-us .zx-contain .contact-info .icons .icon-shape i {
  font-size: 17.316px;
}

.footer .contact-us .zx-contain .contact-info .icons .icon-shape:hover {
  background-color: #008bff;
}

.footer .copyright {
  background-color: #22262f;
}

.footer .copyright .zx-contain {
  height: 56px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer .copyright .zx-contain .copy {
  font-family: reg;
  font-size: 15px;
  color: #8b8d92;
}

.footer .copyright .zx-contain img {
  margin: 0 5px;
}

@keyframes translateX-animation {
  0% {
    left: 22px;
  }
  100% {
    left: 28px;
  }
}

/* ------ footer end ------ */
/* ------ responsive ------ */
.for-mobile {
  display: none;
}

.zx-search-popup {
  display: none;
}

.fixed-menu-container {
  display: none;
}

