    padding: 0 30px;
    transition: all .5s ease-in-out;
  }
  .zx-search-popup .zx-search-form input::placeholder {
    font-family: reg;
    font-size: 14px;
    color: #bbbbbb;
  }
  .zx-search-popup .zx-search-form .zx-search-btn {
    width: 38px;
    height: 38px;
    position: absolute;
    top: 4px;
    left: 7px;
    font-size: 14px;
    color: #FFF;
    background-color: #008bff;
    border: none;
    border-radius: 50%;
    outline: none;
    transition: all .5s ease-in-out;
  }
  .zx-search-popup .zx-search-form .zx-search-btn i {
    transition: all .3s ease-in-out;
  }
  .zx-search-popup .zx-search-form .zx-search-btn:hover i {
    transform: rotateY(360deg);
  }
  .fixed-menu-container {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    position: absolute;
    z-index: 9999;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .fixed-menu-container .fixed-menu {
    width: 260px;
    height: 100%;
    background: #FFF;
    position: absolute;
