  }
  .fixed-menu-container .fixed-menu .nav-list {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    position: relative;
    padding: 0 30px 0 0;
  }
  .fixed-menu-container .fixed-menu .nav-list a {
    text-decoration: none;
    color: #333;
  }
  .fixed-menu-container .fixed-menu .nav-list .close {
    position: absolute;
    top: 16px;
    left: 26px;
    color: #000;
    opacity: .6;
  }
  .fixed-menu-container .fixed-menu .nav-list .user-info {
    margin: 50px 0 0 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: reg;
    font-size: 14px;
  }
  .fixed-menu-container .fixed-menu .nav-list .user-info a {
    text-decoration: none;
    color: #252933;
  }
  .fixed-menu-container .fixed-menu .nav-list .user-info .zx-on-card, .fixed-menu-container .fixed-menu .nav-list .user-info .account, .fixed-menu-container .fixed-menu .nav-list .user-info .fav {
    width: 45px;
    height: 45px;
    border-style: solid;
    border-width: 1px;
    border-color: #ececec;
    border-radius: 50%;
    margin: 0 6px;
    position: relative;
