@import 'main';
// End Of Import

// Start Header




/*
=====
LODEARS
=====
*/


 
.owl-carousel .owl-item img {
    width: auto;
}



/* ------ upper bar start ------ */

.upper-bar {
    background-color: rgb(35, 47, 62);
    width: 100%;
    height: 36px;
    font-family: reg;
    font-size: 13px;
    color: rgb(139, 141, 146);
    .zx-contain {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    a {
        text-decoration: none;
        color: rgb(139, 141, 146);
        transition: all .3s ease-in-out;
        &:hover {
            color: rgb(0, 139, 255);
        }
        .lang {
        }
    }
    .features {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding: 9px 0;
        .feature {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            margin: 0 15px;
            i {
                padding: 0 0 0 9px;
            }
            p {
                padding: 0;
                margin: 0;
            }
        }
    }
}
/* ------ upper bar end ------ */


/* ------ navbar start  ------ */
.fixed {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
    background-color: #FFF;
    padding: 12px 0 0 0 !important;
}
.logo-shrink {
    width: 160px !important;
}
.zx-search-form-top {
    margin: 16px 0 0 0 !important;
}
.user-info-top {
    margin: 16px 0 0 0 !important;
}
.nav-list-top {
    margin: 16px 0 0 0 !important;
}
.nav-bar {
    padding: 18px 0 0 0;
    transition: all .5s ease-in-out;
    //border: 1px solid rgb(244, 244, 244);
    box-shadow: 0px 0px 8px 0px rgba(22, 22, 22, 0.1);
    background-color: #fff;
    .zx-contain {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .logo {
        width: 206px;
        height: auto;
        transition: all .5s ease-in-out;
    }
    .zx-search-form {
        width: 560px;
        position: relative;
        //margin: auto;
        input {
            width: 100%;
            height: 46px;
            outline: none;
            background-color: rgb(245, 245, 245);
            border: none;
            border-radius: 50px;
            padding: 0 30px;
            transition: all .5s ease-in-out;
            &::placeholder {
                font-family: reg;
                font-size: 14px;
                color: rgb(187, 187, 187);
            }
        }
        .zx-search-btn {
            width: 38px;
            height: 38px;
            position: absolute;
            top: 4px;
            left: 7px;
            font-size: 14px;
            color: #FFF;
            background-color: rgb(0, 139, 255);
            border: none;
            border-radius: 50%;
            outline: none;
            transition: all .5s ease-in-out;
            i {
                transition: all .3s ease-in-out;
            }
            &:hover {
                i {
                    transform: rotateY(360deg);
                }
            }
        }
    }
    .user-info {
        //margin-right: auto;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: reg;
        font-size: 14px;
        position: relative;
        a {
            text-decoration: none;
            color: rgb(37, 41, 51);
        }
        .account {
            padding-left: 18px;
            transition: all .5s ease-in-out;
            position: relative;
            i {
                padding-left: 9px;
            }
            &:hover {
                color: rgb(0, 139, 255);
                + .log-in {
                    opacity: 1;
                    visibility: visible;
                }
            }
        }
        .log-in {
            width: 100px;
            height: 60px;
            position: absolute;
            top: 40px;
            right: -4px;
            background-color: #008bff;
            color: #FFF;
            border-radius: 5px;
            padding: 8px 12px;
            opacity: 0;
            visibility: hidden;
            transition: all .5s ease-in-out;
            a {
                text-decoration: none;
                color: #FFF;
                display: block;
                font-family: reg;
            }
            &:hover {
                opacity: 1;
                visibility: visible;
            }
        }
        .fav {
            padding-left: 18px;
            transition: all .5s ease-in-out;
            i {
                padding-left: 9px;
            }
            &:hover {
                color: #008bff;
            }
        }
        .zx-on-card {
            width: 49px;
            height: 49px;
            border-style: solid;
            border-width: 1px;
            border-color: rgb(236, 236, 236);
            border-radius: 50%;
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: all .5s ease-in-out;
            i {
                font-size: 16px;
            }
            span {
                width: 15px;
                height: 15px;
                font-family: reg;
                font-size: 12px;
                line-height: 1.5;
                color: rgb(255, 255, 255);
                background-color: rgb(0, 139, 255);
                border-radius: 50%;
                position: absolute;
                top: 0;
                right: 0;
                display: flex;
                justify-content: center;
                align-items: center;
            }
            &:hover {
                color: rgb(0, 139, 255);
            }
        }
    }
    .nav-list {
        font-family: reg;
        font-size: 16px;
        border: 1px solid #f4f4f4;
        //box-shadow: 0px 0px 8px 0px rgba(22, 22, 22, 0.1);
        margin: 26px 0 0 0;
        ul {
            display: flex;
            justify-content: flex-end;
            align-items: center;
            flex-direction: row-reverse;
            padding: 13px 0;
            margin: 0;
            a {
                text-decoration: none;
                color: rgb(37, 41, 51);
                transition: all .4s ease-in-out;
            }
            li {
                padding: 0;
                margin: 0 0 0 24px;
                transition: all .4s ease-in-out;
                &:hover {
                    a {
                        color: #008AFF;
                    }
                }
            }
        }
    }
}
/* ------ navbar end ------ */

/* ------ main slider start ------ */
.mr-top {
    margin: 190px 0 0 0 !important;
}
.zx-main-slider {
    margin: 20px 0 0 0;
    //transition: all .5s ease-in-out;
    position: relative;
    .owl-carousel .owl-dots{
        position: absolute;
        bottom: 30px;
        left: 50%;
        transform: translateX(-50%);
    }
    .owl-carousel .owl-nav button.owl-next,
    .owl-carousel .owl-nav button.owl-prev,
    .owl-carousel button.owl-dot {
        background: rgba(0, 0, 0, 0.502);
        width: 8px;
        height: 8px;
        border-radius: 50%;
        margin-left: 7px;
        outline: none;
    }
    button.owl-dot.active {
        background: #FFF;
        width: 8px;
        height: 8px;
    }
}
/* ------ main slider end ------ */


/* ------ categories start ------ */
.categories {
    margin: 60px 0 0 0;
    a {
        text-decoration: none;
        font-family: reg;
        font-size: 17px;
        color: rgb(37, 41, 51);
    }
    .zx-contain {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .category {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            .img-container {
                border-radius: 50%;
                background-color: rgb(0, 139, 255);
                width: 140px;
                height: 140px;
                display: flex;
                justify-content: center;
                align-items: center;
                margin: 0 0 22px 0;
                transition: all .5s ease-in-out;
                img {
                    transition: all .5s ease-in-out;
                }
                &:hover {
                    img {
                        //transform: scale(1.05);
                        animation: transformY-animation .5s infinite alternate;
                    }
                }
            }
        }
    }
}
@keyframes transformY-animation {
    0% {
        transform: translateY(-2px);
    }
    100% {
        transform: translateY(2px);
    }
}
/* ------ categories end ------ */

/* ------ special offer start ------ */
.sp-offers {
    a {
        text-decoration: none;
    }
    .zx-offers-header {
        width: 100%;
        height: 48px;
        padding: 0 28px;
        margin: 60px 0 0 0;
        background-color: rgb(247, 247, 247);
        border-right: 3px solid rgb(0, 139, 255);
        display: flex;
        justify-content: space-between;
        align-items: center;
        transition: all .5s ease-in-out;
        h3 {
            font-family: bold;
            font-size: 18px;
            color: rgb(37, 41, 51);
            padding: 0;
            margin: 0;
        }
        a {
            font-family: reg;
            font-size: 14px;
            color: rgb(168, 175, 177);
            padding: 0;
            margin: 0;
            &:hover {
                color: rgb(0, 139, 255);
            }
        }
        &:hover {
            border-right: 6px solid rgb(0, 139, 255);
            padding: 0 25px;
        }
    }
    .zx-card {
        width: 208px;
        height: auto;
        margin: 22px 0 0 0;
        a {
            text-decoration: none;
        }
        .img-container {
            position: relative;
            overflow: hidden;
            width: 208px;
            height: 208px;
            border-style: solid;
            border-width: 1px;
            border-color: rgb(242, 242, 242);
            display: flex;
            justify-content: center;
            align-items: center;
            transition: all .5s ease-in-out;
            img {
                transition: all .5s ease-in-out;
            }
            .overlay {
                position: absolute;
                width: 100%;
                height: 100%;
                background-color: rgba(0,0,0, .3);
                opacity: 0;
                transition: all .5s ease-in-out;
                .offer {
                    width: 60px;
                    height: 30px;
                    position: absolute;
                    left: -61px;
                    top: 12px;
                    background-color: #008bff;
                    color: #FFF;
                    font-family: reg;
                    font-size: 12px;
                    padding: 6px 0 0 0;
                    border: 1px solid #FFF;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    transition: all .5s ease-in-out;
                }
            }
        }
        .zx-card-info {
            width: 208px;
            height: 122px;
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            h4 {
                font-family: reg;
                font-size: 15px;
                color: rgb(37, 41, 51);
                line-height: 1.333;
                text-align: right;
                padding: 0 6px;
                margin: 30px 0 16px 0;
                -moz-transform: matrix( 1,0,0,1.00113259963671,0,0);
                -webkit-transform: matrix( 1,0,0,1.00113259963671,0,0);
                -ms-transform: matrix( 1,0,0,1.00113259963671,0,0);
            }
            .new-price, .old-price {
                display: block;
                padding: 0 6px;
                font-family: reg;
                font-size: 16px;
                line-height: 1.2;
            }
            .new-price {
                color: #008bff;
            }
            .old-price {
                text-decoration: line-through;
                color: #989797;
            }
        }
        &:hover {
            .img-container {
                opacity: .6;
                img {
                    transform: scale(1.06);
                }
                .overlay {
                    opacity: 0;
                    .offer {
                        left: -1px;
                    }
                }   
            }
        }
    }
}
/* ------ special offer end ------ */

/* ------ panners start ------ */
.panners {
    margin: 60px 0 0 0;
    .zx-contain {
        display: flex;
        justify-content: center;
        align-items: center;
        .panner {
            overflow: hidden;
            transition: all .5s ease-in-out;
            img {
                transition: all .5s ease-in-out;
            }
            &:hover {
                img {
                    transform: scale(1.02);
                }
            }
        }
        .panner:first-child {
            margin: 0 0 0 16px;
        }
    }
}
/* ------ panners end ------ */

/* ------ sponsor start ------ */
.sponsor {
    
    a {
        text-decoration: none;
    }
    .zx-offers-header {
        width: 100%;
        height: 48px;
        padding: 0 28px;
        margin: 60px 0 0 0;
        background-color: rgb(247, 247, 247);
        border-right: 3px solid rgb(0, 139, 255);
        display: flex;
        justify-content: space-between;
        align-items: center;
        transition: all .5s ease-in-out;
        h3 {
            font-family: bold;
            font-size: 18px;
            color: rgb(37, 41, 51);
            padding: 0;
            margin: 0;
        }
        a {
            font-family: reg;
            font-size: 14px;
            color: rgb(168, 175, 177);
            padding: 0;
            margin: 0;
        }
        &:hover {
            border-right: 6px solid rgb(0, 139, 255);
            padding: 0 25px;
        }
    }
    .sponsor-logos {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 10px 0 0 0;
        .sponsor-logo {
            width: 156px;
            height: 156px;
            border-style: solid;
            border-width: 1px;
            border-color: rgb(242, 242, 242);
            display: flex;
            justify-content: center;
            align-items: center;
            transition: all .5s ease-in-out;
            &:hover {
                opacity: .6;
            }
        }
    }
}
/* ------ sponsor end ------ */

/* ------ footer start ------ */
.footer {
    margin: 60px 0 0 0;
    a {
        text-decoration: none;
    }
    .subscribe-bar {
        width: 100%;
        height: 97px;
        background-color: rgb(0, 139, 255);
        .zx-contain {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            padding: 31px 0;
            .img-container {

            }
            h3 {
                padding: 0;
                margin: 1px 22px 0 37px;
                font-family: bold;
                font-size: 16px;
                line-height: 1.438;
                color: rgb(255, 255, 255);
            }
            .e-mail {
                width: 609px;
                height: 45px;
                position: relative;
                transition: all .5s ease-in-out;
                input {
                    width: 100%;
                    height: 100%;
                    background-color: rgb(255, 255, 255);
                    border: none;
                    border-radius: 50px;
                    padding: 0 33px 0 26px;
                    outline: none;
                    &::placeholder {
                        font-family: reg;
                        font-size: 14px;
                        color: rgb(140, 139, 139);
                        line-height: 1.643;
                    }
                }
                button {
                    background: transparent;
                    border: none;
                }
                i {
                    font-size: 16px;
                    color: rgb(0, 139, 255);
                    width: 15px;
                    height: 16px;
                    position: absolute;
                    left: 28px;
                    top: 50%;
                    transform: translateY(-50%);
                    transition: all .3s ease-in-out;
                    cursor: pointer;
                }
                &:hover {
                    i {
                        animation: translateX-animation .5s infinite alternate;
                    }
                }
            }
        }
    }
    .contact-us {
        background-color: rgb(37, 41, 51);
        .zx-contain {
            display: flex;
            // justify-content: space-between;
            // align-items: baseline;
            margin: 60px 0 60px 0;
            .contact-info {
                h3 {
                    font-family: bold;
                    font-size: 18px;
                    color: rgb(255, 255, 255);
                    line-height: 1.444;
                    margin: 0 0 36px 0;
                    cursor: default;
                }
                ul {
                    padding: 0;
                    margin: 0;
                    li {
                        font-family: reg;
                        font-size: 15px;
                        color: rgb(255, 255, 255);
                        line-height: 1.444;
                        margin: 0 0 15px 0;
                        transition: all .5s ease-in-out;
                        &:hover {
                            color: #21aac5;
                        }
                        i {
                            width: 12px;
                            font-size: 14px;
                            color: rgb(0, 139, 255);
                            margin: 0 0 0 12px;
                        }
                    }
                    
                }
                .icons {
                    margin: 30px 0 0 0;
                    a {
                        text-decoration: none;
                        color: #FFF;
                    }
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                    .icon-shape {
                        border-radius: 50%;
                        background-color: rgb(54, 58, 67);
                        width: 36px;
                        height: 36px;
                        margin: 0 0 0 5px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        transition: all .5s ease-in-out;
                        i {
                            font-size: 17.316px;
                        }
                        &:hover {
                            background-color: rgb(0, 139, 255);
                        }
                    }
                }
            }
        }
        
    }
    .copyright {
        background-color: rgb(34, 38, 47);
        .zx-contain {
            height: 56px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .copy {
                font-family: reg;
                font-size: 15px;
                color: rgb(139, 141, 146);
            }
            img {
                margin: 0 5px;
            }
            .payment {
    
            }
        }
    }
}
@keyframes translateX-animation {
    0% {
        left: 22px;
    }
    100% {
        left: 28px;
    }
}
/* ------ footer end ------ */


/* ------ responsive ------ */

.for-mobile {
    display: none;
}
.zx-search-popup {
    display: none;
}
.fixed-menu-container {
    display: none;
}

@media (max-width: 992px) {
    .upper-bar {
        display: none;
    }
    .zx-search-popup {
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0, .8);
        position: absolute;
        z-index: 9999;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        .close {
            width: 40px;
            height: 40px;
            color: rgb(255, 255, 255);
            background-color: rgb(0, 139, 255);
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            opacity: .9;
            position: absolute;
            top: 16%;
            left: 50%;
            transform: translateX(-50%);
        }
        .zx-search-form {
            width: 80%;
            position: relative;
            margin: 160px auto 0 auto;
            input {
                width: 100%;
                height: 46px;
                outline: none;
                background-color: rgb(245, 245, 245);
                border: none;
                border-radius: 50px;
                padding: 0 30px;
                transition: all .5s ease-in-out;
