                    font-family: reg;
                    font-size: 14px;
                    color: rgb(187, 187, 187);
                }
            }
            .zx-search-btn {
                width: 38px;
                height: 38px;
                position: absolute;
                top: 4px;
                left: 7px;
                font-size: 14px;
                color: #FFF;
                background-color: rgb(0, 139, 255);
                border: none;
                border-radius: 50%;
                outline: none;
                transition: all .5s ease-in-out;
                i {
                    transition: all .3s ease-in-out;
                }
                &:hover {
                    i {
                        transform: rotateY(360deg);
                    }
                }
            }
        }
    }
    .fixed-menu-container {
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0, .6);
        position: absolute;
        z-index: 9999;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        .fixed-menu {
            width: 260px;
            height: 100%;
            background: #FFF;
            position: absolute;
            top: 0;
            right: -260px;
            .nav-list {
                display: flex;
                justify-content: center;
                align-items: flex-start;
                flex-direction: column;
                position: relative;
                padding: 0 30px 0 0;
                a {
                    text-decoration: none;
                    color: #333;
                }
                .close {
                    position: absolute;
                    top: 16px;
                    left: 26px;
                    color: #000;
                    opacity: .6;
                }
                .user-info {
                    margin: 50px 0 0 0;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-family: reg;
                    font-size: 14px;
                    
                    a {
                        text-decoration: none;
                        color: rgb(37, 41, 51);
                    }
                    .zx-on-card, .account, .fav {
                        width: 45px;
                        height: 45px;
                        border-style: solid;
                        border-width: 1px;
                        border-color: rgb(236, 236, 236);
                        border-radius: 50%;
                        margin: 0 6px;
                        position: relative;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        transition: all .5s ease-in-out;
                        i {
                            font-size: 15px;
                        }
                        span {
                            width: 12px;
                            height: 12px;
                            font-family: reg;
                            font-size: 9px;
                            line-height: 1.5;
                            color: rgb(255, 255, 255);
                            background-color: rgb(0, 139, 255);
                            border-radius: 50%;
                            position: absolute;
                            top: 0;
                            right: 0;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                        }
                    }
                }
                ul {
                    display: flex;
                    // justify-content: center;
                    // align-items: center;
                    flex-direction: column-reverse;
                    padding: 12px 0 0 0;
                    font-family: reg;
                    li {
                        padding: 0 0 9px 0;
                        .lang {
                            //border-bottom: 1px solid #CCC;
                        }
                    }
                }
            }
        }
    }
    .nav-bar {
        padding: 18px 0 18px 0;
        .zx-contain {
            justify-content: center;
            flex-direction: column;
        }
        .for-mobile {
            display: flex;
            justify-content: center;
            align-items: center;
            .menu, .zx-search-btn {
                width: 38px;
                height: 38px;
                margin: 12px 6px 6px 6px;
                font-size: 14px;
                color: #FFF;
                background-color: rgb(0, 139, 255);
                border: none;
                border-radius: 50%;
                outline: none;
                transition: all .5s ease-in-out;
            }
            .zx-on-card{
                text-decoration: none;
                color: #FFF;
                width: 38px;
                height: 38px;
                // border-style: solid;
                // border-width: 1px;
                background-color: rgb(0, 139, 255);
                border-radius: 50%;
                margin: 12px 6px 6px 6px;
                position: relative;
                display: flex;
                justify-content: center;
                align-items: center;
                transition: all .5s ease-in-out;
                i {
                    font-size: 12px;
                }
                span {
                    width: 12px;
                    height: 12px;
                    font-family: reg;
                    font-size: 9px;
                    line-height: 1.5;
                    color: rgb(0, 0, 0);
                    background-color: #f7f7f7;
                    border-radius: 50%;
                    position: absolute;
                    top: 0;
                    right: 0;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
            }
        }
        .zx-search-form {
            display: none;
        }
        .user-info {
            display: none;
        }
        .nav-list {
            display: none;
        }
    }
    .owl-carousel .owl-dots {
        position: absolute;
        left: 50%;
        bottom: -30px;
        transform: translateX(-50%);
        .active {
            background: #008bff !important;
        }
    }
    .owl-carousel .owl-nav button.owl-next,
    .owl-carousel .owl-nav button.owl-prev,
    .owl-carousel button.owl-dot {
        width: 8px;
        height: 8px;
        background-color: #DDD;
        border-radius: 50%;
        margin: 0 5px;
        outline: none;
        
    }
    .panners .zx-contain {
        flex-direction: column;
        .panner {
            margin: 6px 0 !important;
        }
    }
    .footer .subscribe-bar {
        height: auto;
        .zx-contain {
            flex-direction: column;
            padding: 20px 0;
            h3 {
                margin: 6px 0;
            }
        }
    }
    .footer .contact-us .zx-contain {
        flex-direction: column;
        margin: 20px 0 20px 0;
        .contact-info {
            h3 {
                margin: 0 0 12px 0;
                cursor: pointer;
                &::before {
                    content: '\f104';
                    color: #FFF;
                    transition: all .25s;
                    font-family: 'Font Awesome 5 Free';
                    font-weight: 900;
                    position: absolute;
                    top: 0;
                    right: 190px;
                }
            }
            h3.active {
                color: #008bff;
                &::before {
                    content: '\f107';
                    color: #008bff;
                    transition: all .25s;
                    font-family: 'Font Awesome 5 Free';
                    font-weight: 900;
                    position: absolute;
                    top: 0;
                    right: 190px;
                }
            }
            ul {
                display: none;
                //margin: 0 0 30px 0;
                li {
                    font-size: 14px;
                    margin: 0 0 12px 0;
                }
            }
            .icons {
                margin: 26px 0 0 0;
                justify-content: center;
            }
        }
    }
    .footer .copyright .zx-contain {
        height: auto;
        padding: 15px 0;
        flex-direction: column;
        .copy {
            text-align: center;
            order: 2;
            .block {
                display: block;
                margin: 5px 0;
            }
        }
        .payment {
            order: 1;
        }
    }
}
@media (max-width: 767px) {
    .footer .subscribe-bar .zx-contain .e-mail {
        width: 80%;
    }
}
@media (max-width: 600px) {
    .categories {
        margin: 30px 0 30px 0;
    }
    .sp-offers .zx-card {
        margin: 16px auto 16px auto;
    }
    .sp-offers .zx-offers-header {
        margin: 30px 0 0 0;
        padding: 0 12px;
    }
    .panners {
        margin: 50px 0 0 0;
    }
    .sponsor .sponsor-logos .sponsor-logo {
        margin: 16px auto 16px auto;
    }
    .footer .contact-us .zx-contain {
        padding: 0 30px;
    }
}
@media (max-width: 375px) {
    .nav-bar .logo {
        width: 160px;
    }
    .zx-main-slider .owl-carousel .owl-dots {
        bottom: -20px;
    }
    .categories {
        margin: 50px 0 20px 0;
        .zx-contain .category .img-container {
            margin: 0 0 12px 0;
        }
    }
    .owl-carousel .owl-dots {
        bottom: -20px;
    }
    .sp-offers .zx-offers-header h3 {
        font-size: 16px;
    }
    .sp-offers .zx-offers-header p {
        font-size: 13px;
    }
    .sp-offers .zx-card .zx-card-info {
        h4 {
            text-align: center;
        }
        .price {
            text-align: center;
        }
    }
    .footer .subscribe-bar .zx-contain h3 {
        font-size: 14px;
        text-align: center;
    }
    .footer .subscribe-bar .zx-contain .e-mail {
        width: 96%;
        height: 40px;
    }
}